import React, { useEffect, useState } from 'react';

function Invitation({ onBack, showbutton = false }) {
  const [isSlideIn, setIsSlideIn] = useState(false);
  const [isImagesLoaded, setIsImagesLoaded] = useState(false);

  const imageSources = [
    '/static/stockimages/invitation_background.webp',
    '/static/icons/back_double.webp',
    '/static/stockimages/moon.webp',
    '/static/stockimages/invitext.webp'
  ];

  // Preload images function
  const preloadImages = (sources) => {
    return Promise.all(
      sources.map((src) => {
        return new Promise((resolve, reject) => {
          const img = new Image();
          img.src = src;
          img.onload = resolve;
          img.onerror = reject;
        });
      })
    );
  };

  useEffect(() => {
    // Preload images before setting slide-in effect
    preloadImages(imageSources)
      .then(() => {
        setIsImagesLoaded(true); // Images are fully loaded
        setTimeout(() => setIsSlideIn(true), 50);
      })
      .catch((error) => {
        console.error('Error loading images:', error);
      });
  }, []);

  const handleBack = () => {
    setIsSlideIn(false); //   
    //     
    setTimeout(() => {
      onBack(); //    onBack 
    }, 700); //   0.7 700ms  
  };

  if (!isImagesLoaded) {
    return <div></div>; // You can replace this with a custom loader
  }

  return (
    <div
      style={{
        position: 'fixed',
        top: 0,
        right: isSlideIn ? '0' : '-100%', //  /   right 
        width: '100%',
        height: '100%',
        transition: 'right 0.7s ease', //   
        backgroundColor: '#412823 !important',
        overflow: 'hidden',
        zIndex: '99999999',
      }}
    >
      <div
        id="invitation-background"
        style={{
          position: 'absolute',
          top: 0,
          left: 0,
          width: '100%',
          height: '100%',
          overflow: 'hidden',
          zIndex: '999999 !important',
          opacity: 1,
        }}
      >
        <img
          src="/static/stockimages/invitation_background.webp"
          alt="background"
          style={{
            width: '100%',
            height: '100%',
            objectFit: 'cover',
            pointerEvents: 'none',
          }}
        />
      </div>

      {/* Header */}
      <div
        style={{
          position: 'absolute',
          top: 0,
          right: isSlideIn ? '0' : '-150%',
          width: '100%',
          transition: 'right 0.7s ease',
          height: '58px',
          zIndex: 1000,
          overflow: 'hidden',
          boxShadow: 'none',
          backgroundColor: 'transparent',
        }}
      >
        <div
          style={{
            color: '#f8f6f1',
            width: '100%',
            height: '100%',
            display: 'flex',
            justifyContent: 'space-between',
            background: 'transparent',
            alignItems: 'center',
            padding: '0 15px',
            boxSizing: 'border-box',
            position: 'relative',
          }}
        >
          <div
            style={{
              left: '15px',
              width: '30px',
            }}
          >
            <img
              src="/static/icons/back_double.webp"
              alt="back"
              onClick={handleBack}
              style={{
                width: '100%',
                height: '100%',
                filter: 'brightness(0) invert(1)',
              }}
            />
          </div>
          <div style={{ fontSize: '20px' }}> </div>
          <div style={{ width: '7%' }}></div>
        </div>
      </div>

      <img
        src="/static/stockimages/moon.webp"
        alt="moon"
        style={{
          position: 'sticky',
          left: '40.06%',
          right: '6.62%',
          top: '8.91%',
          bottom: '64.08%',
          width: '47.33%',
          filter: 'blur(5px)',
          zIndex: 1,
          pointerEvents: 'none',
        }}
      />

      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          width: '100%',
          height: '60%',
          zIndex: 999,
          top: '-10%',
          position: 'relative',
        }}
      >
        <img
          src="/static/stockimages/invitext.webp"
          alt="invitation text"
          style={{
            width: '100%',
            height: '100%',
            objectFit: 'contain',
            marginTop: '10%',
            pointerEvents: 'none',
          }}
        />
      </div>

      {/* Button container */}
      {showbutton && (
        <div
          style={{
            position: 'fixed',
            bottom: '10%',
            right: isSlideIn ? '0%' : '-150%',
            left: 0,
            zIndex: 999,
            display: 'flex',
            justifyContent: 'center',
            transition: 'right 0.7s ease',
          }}
        >
          <a
            href="/CreateCharacter"
            style={{
              display: 'inline-block',
              width: '170px',
              height: '35px',
              lineHeight: '35px',
              textAlign: 'center',
              boxSizing: 'border-box',
              background: '#f8f6f1',
              border: '1px solid #e6e1dc',
              boxShadow: '2px 2px 4px rgba(0, 0, 0, 0.25)',
              textDecoration: 'none',
              color: 'inherit',
            }}
            onMouseEnter={(e) => {
              e.target.style.background = '#ed9a9a';
              e.target.style.color = '#f8f6f1';
            }}
            onMouseLeave={(e) => {
              e.target.style.background = '#f8f6f1';
              e.target.style.color = 'inherit';
            }}
          >
             
          </a>
        </div>
      )}
    </div>
  );
}

export default Invitation;
